s /* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */
aside.qodef-sidebar {
	position: relative;
	z-index: 11;
	margin: 0 0 60px;
}

.wpb_widgetised_column {
	position: relative;
}

/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

/*
* $title-adjusted-size variable is default body ( line-height - font-size ) / 2 + 3 where 3 is temporary value, it's half size of widget title line height minus font size,
* $wp-widgets-map input-fields-... variables are for select and for search elements
*/
$title-adjusted-size: (26 - 12) / 2 + 3;

$wp-widgets-map: (
		widget-margin: 60,
		widget-title-margin: 22,
		widget-title-adjusted-margin: $title-adjusted-size,
		widget-title-color: null,
		list-item-bottom-margin: 13,
		text-color: null,
		link-color: $default-text-color,
		link-hover-color: $default-hover-color,
		list-link-color: null,
		list-link-hover-color: null,
		input-fields-height: $input-height,
		input-fields-side-padding: $input-horizontal-padding,
		input-fields-text-size: 14,
		input-fields-text-line-height: 24,
		input-fields-text-color: $default-text-color,
		input-fields-focus-text-color: $default-text-color,
		input-fields-background-color: transparent,
		input-fields-focus-background-color: transparent,
		input-fields-border-color: transparent,
		input-fields-focus-border-color: transparent,
		input-fields-button-color: $default-text-color,
		input-fields-button-hover-color: $default-hover-color
);

$wp-widgets-area-map: (
		'': (),
		'.qodef-top-bar' : (
				widget-margin: 0,
				widget-title-color: #fff,
				text-color: #fff,
				link-color: #fff
		),
		'.qodef-page-footer': (
				widget-title-margin: 26,
				widget-title-color: #fff,
				text-color: #fff,
				link-color: #fff,
				input-fields-text-color: #fff,
				input-fields-focus-text-color: #fff,
				input-fields-button-color: #fff,
				input-fields-button-hover-color: $first-main-color,
				letter-spacing: 0
		),
		'.qodef-content-bottom-white-text': (
				widget-title-margin: 26,
				widget-title-color: #fff,
				text-color: #fff,
				link-color: #fff,
				input-fields-text-color: #fff,
				input-fields-focus-text-color: #fff,
				input-fields-button-color: #fff,
				input-fields-button-hover-color: $first-main-color,
				letter-spacing: 0
		),
		'.qodef-content-bottom-dark-text': (
				widget-title-margin: 26,
				widget-title-color: #222222,
				text-color: #222222,
				link-color: #222222,
				input-fields-text-color: #222222,
				input-fields-focus-text-color: #222222,
				input-fields-button-color: #222222,
				input-fields-button-hover-color: $first-main-color,
				letter-spacing: 0
		),
		'.qodef-side-menu': (
				widget-margin: 48,
				widget-title-margin: 26,
				letter-spacing: 0
		)
);

@each $key, $values in $wp-widgets-area-map {
	$wp-widgets-map-merged: map-merge($wp-widgets-map, $values);

	#{$key} a.qodef-icon-widget-holder {
		@if (map-get($wp-widgets-map-merged, 'link-color') != null) {
			color: map-get($wp-widgets-map-merged, 'link-color');

			@if (map-get($wp-widgets-map-merged, 'link-hover-color') != null) {
				&:hover {
					color: map-get($wp-widgets-map-merged, 'link-hover-color');
				}
			}
		}
	}

	#{$key} .widget {
		margin: 0 0 #{map-get($wp-widgets-map-merged, 'widget-margin')}px;

		@if (map-get($wp-widgets-map-merged, 'letter-spacing') != null) {
			letter-spacing: map-get($wp-widgets-map-merged, 'letter-spacing');
		}

		@if (map-get($wp-widgets-map-merged, 'text-color') != null) {
			color: map-get($wp-widgets-map-merged, 'text-color');
		}

		&:last-child {
			margin-bottom: 0 !important;
		}

		.qodef-widget-title {
			margin: -4px 0 #{map-get($wp-widgets-map-merged, 'widget-title-margin')}px;

			@if (map-get($wp-widgets-map-merged, 'widget-title-color') != null) {
				color: map-get($wp-widgets-map-merged, 'widget-title-color');
			}

			a {
				color: inherit;

				&:hover {
					color: map-get($wp-widgets-map-merged, 'link-hover-color');
				}
			}
		}

		@if (map-get($wp-widgets-map-merged, 'link-color') != null) {
			a {
				color: map-get($wp-widgets-map-merged, 'link-color');

				@if (map-get($wp-widgets-map-merged, 'link-hover-color') != null) {
					&:hover {
						color: map-get($wp-widgets-map-merged, 'link-hover-color');
					}
				}
			}
		}

		ul:not(.qodef-shortcode-list) {
			padding: 0;
			margin: 0;
			list-style: none;

			li {
				margin: 0 0 #{map-get($wp-widgets-map-merged, 'list-item-bottom-margin')}px !important;

				&:last-child {
					margin-bottom: 0;
				}

				@if (map-get($wp-widgets-map-merged, 'list-link-color') != null) {
					a {
						color: map-get($wp-widgets-map-merged, 'list-link-color');

						@if (map-get($wp-widgets-map-merged, 'list-link-hover-color') != null) {
							&:hover {
								color: map-get($wp-widgets-map-merged, 'list-link-hover-color');
							}
						}
					}
				}

				ul {
					margin-top: #{map-get($wp-widgets-map-merged, 'list-item-bottom-margin')}px;
					margin-left: #{map-get($wp-widgets-map-merged, 'list-item-bottom-margin')}px;
				}
			}
		}

		&.widget_archive,
		&.widget_categories,
		&.widget_text {

			select {
				width: 100%;
				max-width: 100%;
				height: #{map-get($wp-widgets-map-merged, 'input-fields-height')}px;
				margin: #{map-get($wp-widgets-map-merged, 'widget-title-adjusted-margin')}px 0 0;
				padding: #{(map-get($wp-widgets-map-merged, 'input-fields-height') - map-get($wp-widgets-map-merged, 'input-fields-text-line-height')) / 2}px #{map-get($wp-widgets-map-merged, 'input-fields-side-padding')}px;
				font-family: inherit;
				font-size: #{map-get($wp-widgets-map-merged, 'input-fields-text-size')}px;
				line-height: #{map-get($wp-widgets-map-merged, 'input-fields-text-line-height')}px;
				font-style: inherit;
				font-weight: inherit;
				color: map-get($wp-widgets-map-merged, 'input-fields-text-color');
				background-color: map-get($wp-widgets-map-merged, 'input-fields-background-color');
				border: 1px solid #{map-get($wp-widgets-map-merged, 'input-fields-border-color')};
				border-radius: 0;
				outline: none;
				box-sizing: border-box;
			}
		}

		&.widget_categories {
			margin-bottom: 54px;

			span.select2 {
				margin: 0 0 -10px;
			}
		}

		&.widget_archive {
			margin: 0 0 54px;

			span.select2 {
				margin: 0 0 -11px;
			}
		}

		&.widget_recent_entries {

			.qodef-widget-title {
				margin-bottom: 29px;
			}

			.qodef-recent-posts-widget-table {
				display: table;

				.qodef-recent-posts-widget-left-holder {
					display: table-cell;
					vertical-align: middle;
					overflow: hidden;
					min-width: 100px;
					overflow: hidden;

					img {
						object-fit: cover;
						width: 100px;
						height: 121px;
						display: block;
						transform: scale(1.05) translateX(-3px);
						transition: .5s;
						backface-visibility: hidden;
					}
				}

				.qodef-recent-posts-widget-right-holder {
					display: table-cell;
					vertical-align: middle;
					padding-left: 20px;

					.post-date {
						display: block;
						font-size: 14px;
					}

					.post-title {
						display: block;
						@include qodefDefaultHeadingStyle();
						@include qodefH6();
						margin: 4px 0 19px;
						position: relative;
						transition: color .2s ease-out;

						&:after {
							content: '';
							display: block;
							height: 1px;
							position: absolute;
							bottom: -14px;
							left: -2px;
							width: 39px;
							background-color: #000;
							transition: width .2s ease-out;
						}
					}
				}

				&:hover {

					.qodef-recent-posts-widget-left-holder {

						img {
							transform: scale(1.05) translateX(3px);
						}
					}
				}
			}
		}

		&.widget_calendar {
			width: 100%;
			margin: 0 0 48px;

			#today {
				color: #fff;
				background-color: map-get($wp-widgets-map-merged, 'link-hover-color');

				a {
					color: inherit;
				}
			}

			tfoot {
				@if (map-get($wp-widgets-map-merged, 'link-color') != null) {
					a {
						color: map-get($wp-widgets-map-merged, 'link-color');

						&:hover {
							color: map-get($wp-widgets-map-merged, 'link-hover-color');
						}
					}
				}
			}
		}

		&.widget_text {

			img {
				display: block;
			}

			span.select2 {
				margin: 0 0 -16px;
			}
		}

		&.widget_rss {

			ul {

				li {
					margin-bottom: 30px;

					&:last-child {
						margin-bottom: 0;
					}
				}

				.rsswidget {
					display: block;
					@include qodefDefaultHeadingStyle();
					@include qodefH6();
					color: map-get($wp-widgets-map-merged, 'link-color');
					margin: 0;

					&:hover {
						color: map-get($wp-widgets-map-merged, 'link-hover-color');
					}
				}

				.rss-date {
					margin: 13px 0 10px 0;
					display: block;
				}

				.rssSummary {
					margin-top: 5px;
				}

				cite {
					display: block;
					margin: 10px 0 24px;
				}
			}
		}

		&.widget_search,
		&.widget_product_search {
			margin: 0 0 45px;

			.qodef-widget-title {
				margin-bottom: #{map-get($wp-widgets-map-merged, 'widget-title-margin') + map-get($wp-widgets-map-merged, 'widget-title-adjusted-margin')}px;
			}

			.input-holder {
				@include qodefRelativeHolderLayout();
			}

			input,
			button {
				height: #{map-get($wp-widgets-map-merged, 'input-fields-height')}px;
				padding: #{(map-get($wp-widgets-map-merged, 'input-fields-height') - map-get($wp-widgets-map-merged, 'input-fields-text-line-height')) / 2}px 0;
				font-family: inherit;
				font-weight: inherit;
				line-height: #{map-get($wp-widgets-map-merged, 'input-fields-text-line-height')}px;
				border-radius: 0;
				outline: 0;
				-webkit-appearance: none;
				box-sizing: border-box;
			}

			input {
				@include qodefRelativeHolderLayout();
				padding-left: 30px;
				padding-right: 30px;
				font-size: #{map-get($wp-widgets-map-merged, 'input-fields-text-size')}px;
				color: map-get($wp-widgets-map-merged, 'input-fields-text-color');
				background-color: map-get($wp-widgets-map-merged, 'input-fields-background-color');
				border: 1px solid map-get($wp-widgets-map-merged, 'input-fields-border-color');
				border-bottom: 1px solid $default-border-color;
				transition: border-color .2s ease-out;

				&:focus {
					color: map-get($wp-widgets-map-merged, 'input-fields-focus-text-color');
					background-color: map-get($wp-widgets-map-merged, 'input-fields-focus-background-color');
					border-color: map-get($wp-widgets-map-merged, 'input-fields-focus-border-color');
					border-bottom: 1px solid $default-border-color;
				}

				@include qodefPlaceholder {
					color: inherit;
				}
			}

			button {
				position: absolute;
				top: 0;
				left: 0;
				width: 30px;
				font-size: 16px;
				text-align: left;
				color: map-get($wp-widgets-map-merged, 'input-fields-button-color');
				background-color: transparent;
				border: 0;
				cursor: pointer;
				transition: color .2s ease-out;

				span {
					display: block;
					line-height: inherit;

					&:before {
						display: block;
						line-height: inherit;
					}
				}

				&:hover {
					color: map-get($wp-widgets-map-merged, 'input-fields-button-hover-color');
				}
			}
		}

		&.widget_tag_cloud,
		&.widget_product_tag_cloud {
			margin: 0 0 43px;

			.tagcloud {

				a {
					display: inline-block;
					margin: 0 8px 10px 0;
				}
			}
		}

		&.widget_media_image {

			.qodef-widget-title {
				margin-bottom: #{map-get($wp-widgets-map-merged, 'widget-title-margin') + map-get($wp-widgets-map-merged, 'widget-title-adjusted-margin')}px;
			}

			a, img {
				display: block;
			}
		}

		&.qodef-contact-form-7-widget {

			.ajax-loader {
				position: absolute;
				bottom: 12px;
				right: -15px;
			}
		}
	}

	/* Overriding side area */
	.qodef-side-menu,
	.qodef-enquire-menu,
	.qodef-vertical-area-widget-holder {
		color: #000;

		/* global */
		.qodef-widget-title {
			@include qodefH4();

			&:after {
				content: '';
				width: 39px;
				height: 1px;
				background-color: currentColor;
				display: block;
				margin: 20px 0 0;
			}
		}

		/* different widgets */
		.widget_text {
			font-size: 17px;
			line-height: 25px;

			.qodef-widget-title {
				margin-bottom: 18px;
			}
		}

		.qodef-icon-widget-holder {
			transform: translateY(-11px);
			display: block;
			color: #000;
		}

		.widget.qodef-social-texts-group-widget {
			margin-top: 11px;
		}
	}

	/* Overriding footer */
	.qodef-page-footer, .qodef-content-bottom {

		/* global*/
		.qodef-widget-title {
			@include qodefH4();
		}

		/* custom elements */
		.select2-selection__arrow {
			color: #fff !important;
		}

		.select2-selection__rendered {
			color: #fff !important;
			padding-right: 20px !important;
		}

		/* different widgets */
		.widget.widget_calendar {

			#today {
				color: #000;
			}
		}

		.qodef-icon-element {
			text-align: center;
		}

		.widget.qodef-social-texts-group-widget {
			margin-top: -30px;

			@media screen and (max-width: 1024px) {
				margin-bottom: 53px !important;
			}
		}

		.widget.widget_custom_html {
			margin-bottom: 0;
		}

		/* Footer Top */
		.qodef-footer-top-holder {

			.qodef-icon-widget-holder {
				transform: translateY(-7px);
				display: block;
			}
		}

		/* Footer Bottom */
		.qodef-footer-bottom-holder {
			text-align: right;

			.widget:last-child {
				margin-bottom: 60px !important;

				@media screen and (max-width: 1024px) {
					margin-bottom: 0 !important;
				}
			}

			@media screen and (max-width: 1024px) {
				text-align: center;

				.widget_media_image {

					img {
						display: inline-block;
					}
				}
			}

			@media screen and (max-width: 680px) {
				.qodef-icon-widget-holder {
					display: block;
					margin: 25px 0!important;
				}
			}
		}
	}

	/* Overriding content bottom white text */
	.qodef-content-bottom-white-text {

		/* custom elements */
		.select2-selection__arrow {
			color: #fff !important;
		}

		.select2-selection__rendered {
			color: #fff !important;
			padding-right: 20px !important;
		}

		.qodef-social-icon-widget, .qodef-social-icon-widget-name-holder {
			color: #fff !important;
		}

		#respond textarea,
		.qodef-style-form textarea,
		#respond input[type='text'],
		input[type='text'],
		input[type='email'],
		input[type='password'],
		.post-password-form input[type='password'] {
			color: #fff;
			border-color: transparent transparent rgba(255, 255, 255, 0.25) !important;

			&:focus {
				color: #fff;
				border-color: transparent transparent rgba(255, 255, 255, 0.25) !important;
			}
		}

		.wpcf7-form-control {
			&.wpcf7-text,
			&.wpcf7-number,
			&.wpcf7-date,
			&.wpcf7-textarea,
			&.wpcf7-select,
			&.wpcf7-quiz {
				color: #fff;
				border-color: transparent transparent rgba(255, 255, 255, 0.25) !important;

				&:focus {
					color: #fff;
					border-color: transparent transparent rgba(255, 255, 255, 0.25) !important;
				}
			}

			&.wpcf7-submit {

				i {
					color: #fff;
				}
			}
		}

		.qodef-btn.qodef-btn-outline:not(.qodef-btn-custom-border-hover) {
			color: #fff !important;

			&:hover {
				color: #fff !important;
				border-color: #fff !important;
			}
		}
	}

	/* Overriding content bottom white text */
	.qodef-content-bottom-dark-text {

		/* custom elements */
		.qodef-content-bottom .qodef-content-bottom-predefined .widget:nth-child(1):after {
			background-color: rgba(0, 0, 0, 0.25);
		}

		.select2-selection__arrow {
			color: #222 !important;
		}

		.select2-selection__rendered {
			color: #222 !important;
			padding-right: 20px !important;
		}

		.qodef-social-icon-widget, .qodef-social-icon-widget-name-holder {
			color: #222 !important;
		}

		#respond textarea,
		.qodef-style-form textarea,
		#respond input[type='text'],
		input[type='text'],
		input[type='email'],
		input[type='password'],
		.post-password-form input[type='password'] {
			color: #222;
			border-color: transparent transparent #b2b9bb !important;

			&:focus {
				color: #222;
				border-color: transparent transparent #b2b9bb !important;
			}
		}

		.wpcf7-form-control {
			&.wpcf7-text,
			&.wpcf7-number,
			&.wpcf7-date,
			&.wpcf7-textarea,
			&.wpcf7-select,
			&.wpcf7-quiz {
				color: #222;
				border-color: transparent transparent #b2b9bb !important;

				&:focus {
					color: #222;
					border-color: transparent transparent #b2b9bb !important;
				}
			}

			&.wpcf7-submit {

				i {
					color: #222;
				}
			}
		}

		.qodef-btn.qodef-btn-outline:not(.qodef-btn-custom-border-hover) {
			color: #222 !important;

			&:hover {
				color: #222 !important;
				border-color: #dddddd !important;
			}
		}
	}
}

/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */

/* ==========================================================================
   Search appearance tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	display: none;
}

// IE
input[type="search"]::-ms-clear {
	display: none;
}

input[type=text]::-ms-clear {
	display: none;
}