@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.qodef-comment-holder {
	@include qodefRelativeHolderLayout();
	padding: 0;
	box-sizing: border-box;

	.qodef-comment-holder-inner {
		padding: 17px 0 20px;
	}

	.qodef-comments-title {
		margin-bottom: 28px;
	}

	.qodef-comment {

		&.qodef-pingback-comment {

			.qodef-comment-text {
				padding: 0;
			}
		}
	}

	.qodef-comment-list {
		position: relative;
		padding: 0;
		margin: 0;
		list-style: none;
		z-index: 150;

		.qodef-comment-name {
			margin: 0 !important;
		}

		li {
			@include qodefRelativeHolderLayout();
			margin: 10px 0 0;
			padding: 15px 0 0;
		}

		> li:first-child {
			margin: 0;
			padding: 0;
			border: 0;
		}

		.children {
			padding: 0 0 0 108px;
		}
	}

	.qodef-comment-image {
		position: relative;
		top: 10px;
		left: 10px;
		width: 82px;
		height: 82px;
		display: block;
		float: left;

		img {
			display: block;
			border-radius: 50%;
		}
	}

	.qodef-comment-text {
		padding: 10px 0 0 118px;
		min-height: 82px;

		.qodef-comment-info {
			@include qodefRelativeHolderLayout();
			margin: 3px 0 4px;
		}

		.replay,
		.comment-reply-link,
		.comment-edit-link {
			float: right;
			margin-top: 6px;
			margin-right: 10px;
			font-size: 12px;
			color: $first-main-color;

			&:hover {
				color: $default-heading-color;
			}
		}

		.replay,
		.comment-reply-link,
		.comment-edit-link {
			font-family: $default-text-font;
			float: left;
			color: #222222;
			text-transform: capitalize;
			font-size: 14px;
			font-weight: 400;
			line-height: 30px;

		}

		.qodef-comment-date {
			float: left;
			margin: 0;
			font-size: 14px;
			color: #7d7d7d;
		}

		.qodef-text-holder p {
			margin: 7px 0;
		}

		.comment-respond {
			margin: 43px 0;

			.comment-reply-title {
				margin: 0 0 10px;
				font-size: 14px;
			}

			.logged-in-as {
				margin: 0 0 8px;
			}
		}

		#cancel-comment-reply-link {
			margin-left: 10px;
			font-size: 12px;
			background-color: $first-main-color;

			&:hover {
				color: $default-heading-color;
			}
		}

		ul, ol {

			li {
				display: list-item;
			}
		}

		li {
			margin: 0 !important;
			padding: 0 !important;
			border: 0 !important;
		}
	}
}

.qodef-comment-form {
	@include qodefRelativeHolderLayout();
	margin: 80px 0 40px;
	clear: both;

	@include ipad-landscape {
		padding: 80px 0 16px;
	}

	.comment-reply-title {
		margin: 0 0 55px;
	}

	.logged-in-as {
		margin: 0 0 8px;
	}

	.qodef-icon-element, i {
		margin-right: 10px;
		color: #bbb;
		position: relative;
	}
	.form-submit {
		margin-top: 56px;
	}
}

@include ipad-portrait {

	.qodef-comment-holder {

		.qodef-comment-list {

			.children {
				padding: 0 0 0 40px;
			}
		}
	}
}

@include phone-landscape {

	.qodef-comment-holder {

		.qodef-comment-list {

			.children {
				padding: 0;
			}
		}

		.qodef-comment-text {

			.qodef-comment-date,
			.qodef-comment-name {
				float: none;
			}

			.replay,
			.comment-reply-link,
			.comment-edit-link {
				float: none;
				margin: 0 10px 0 0;
			}
		}
	}
}

@include phone-portrait {

	.qodef-comment-holder {

		.qodef-comment-image {
			float: none;
		}

		.qodef-comment-text {
			padding: 20px 0 0;
			min-height: inherit;
		}
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.qodef-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		border-color: transparent transparent #000 !important;
		padding: 4px 0;
		margin: 15px 0 0;
		line-height: 23px;

		footer & {
			margin: 0;
		}
	}
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit'] {
	@include qodefButtonDefaultStyle();
	@include qodefButtonSize();
	@include qodefButtonSolidColor();

	cursor: pointer;

	&:hover {
		@include qodefButtonSolidHoverColor();
	}
}

#respond input[type="text"]:last-of-type:not(:last-child) {
	margin-bottom: 0;
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

.wpcf7-form-control.wpcf7-submit {
	margin-top: 40px;

	i {
		margin-right: 10px;
		color: #bbb;
		position: relative;

		&:before {
			transition: .2s;
			transition-delay: .2s;
		}

		&:after {
			content: '';
			position: absolute;
			background: currentColor;
			width: 8px;
			height: 8px;
			border-radius: 50%;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-10%);
			opacity: 0;
			transition: .2s;
		}
	}

	&:hover {

		i {
			&:before {
				opacity: 0;
				transform: translateY(-40%);
				transition-delay: 0s;
			}

			&:after{
				opacity: 1;
				transition-delay: .1s;
				transform: translate(-50%,-50%);
			}
		}
	}
}

#respond textarea,
.qodef-style-form textarea,
.wpcf7-form textarea {
	display: block;
}

.qodef-two-columns-form-without-space {
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.qodef-owl-slider,
.qodef-pagination-slider,
.qodef-img-slider,
.qodef-pl-fullscreen-slider,
.qodef-pi-feature-slider {
	visibility: hidden;

	.owl-stage-outer {
		z-index: 1;
	}

	&.qodef-slider-is-moving {
		.owl-item {
			.qodef-block-drag-link {
				z-index: -1 !important;
			}
		}
	}

	&.qodef-slider-has-both-nav {
		.owl-nav {
			.owl-prev,
			.owl-next {
				$pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
				transform: translateY($pos);
			}
		}
	}

	&.qodef-slider-hide-pagination .owl-dots {
		display: none;
	}

	.owl-nav {

		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			padding: 0;
			margin: 0;
			color: $default-heading-color;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			z-index: 3;

			transform: translateY(-50%);
			transition: color .2s ease-out;

			&:hover {
				color: inherit;
			}

			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 46px;
				line-height: 1;

				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}

		.owl-prev {
			left: 30px;
		}

		.owl-next {
			right: 30px;
		}
	}

	.owl-dots {
		text-align: center;
		height: 36px;
		padding: 0 10px;
		counter-reset: section;

		.owl-dot {
			display: inline-flex;
			align-items: center;
			margin: 0;
			padding: 0 6px;
			font-family: $default-heading-font;
			font-size: 13px;
			letter-spacing: .25em;
			cursor: pointer;
			color: #000;
			border: 0;
			background: none;
			outline: none;

			&:before {
				content: '';
				flex: 1 0 auto;
				position: relative;
				height: 1px;
				width: 0;
				transition: width .25s ease;
				background: currentColor;
			}

			&:after {
				content: "0" counter(section);
				position: relative;
				display: block;
				counter-increment: section;
			}

			&.active {
				font-size: 16px;

				&:before {
					width: 100px;
					margin-right: 10px;

					@include phone-landscape {
						width: 20px;
					}
				}

				&:after {
					pointer-events: none;
					cursor: default;
				}
			}

			&:first-child {
				padding-left: 0;
			}

			&:last-child {
				padding-right: 0;
			}
		}
	}

	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}

	+ .qodef-slider-thumbnail {
		display: inline-block;
		vertical-align: middle;
		list-style: none;
		margin: 20px -10px 0;

		> .qodef-slider-thumbnail-item {
			float: left;
			padding: 0 10px;
			margin-bottom: 20px;
			box-sizing: border-box;
			line-height: 0;
			cursor: pointer;

			img {
				border: 2px solid transparent;

				transition: border-color 0.2s ease-out;
			}

			&.active {
				img {
					border: 2px solid $first-main-color;
				}
			}
		}

		$thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

		@for $i from 0 to length($thumbnail_columns_number) {
			&.qodef-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

				.qodef-slider-thumbnail-item {
					width: 100% / ($i+2);

					@include ipad-landscape {
						width: 33.3333%;
					}
				}
			}
		}
	}
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
	.qodef-wrapper {
		padding: 0 !important;
	}

	&.admin-bar {
		.qodef-content, .qodef-content-inner {
			height: calc(100vh - 32px);
		}
	}

	.qodef-content-inner {
		@include qodefTableLayout();
		height: 100vh;
	}

	.qodef-page-not-found {
		@include qodefTableCellLayout();
		max-width: 340px;
		padding-left: 16%;
		background-image: url("../img/404-bg-bottom-left.png"), url("../img/404-bg-image.png");
		background-repeat: no-repeat, no-repeat;
		background-position: 0 100%, 80% 44%;

		@media screen and (max-width: 1440px) {
			background-size: 20%, 50%;
		}

		@media screen and (max-width: 1024px) {
			background-size: 30%, 50%;
		}

		.qodef-row-background-text-wrapper-inner {
			font-size: 300px;
			padding-bottom: 4%;
			padding-right: 12%;

			@media screen and (max-width: 1440px) {
				font-size: 200px;
			}
		}

		.qodef-404-title {
			margin: 0;
			max-width: 340px;
			position: relative;
			z-index: 10;

			&:after {
				content: '';
				width: 75px;
				height: 2px;
				background-color: #222;
				display: block;
				margin: 23px 0;
			}
		}

		.qodef-404-text {
			margin: 10px 0 0;
			letter-spacing: 0;
			font-size: 16px;
			line-height: 26px;
			max-width: 340px;
			position: relative;
			z-index: 10;
		}

		.qodef-btn {
			margin: 30px 0 0;
			position: relative;
			z-index: 10;

			.qodef-icon-font-elegant {
				color: #bbb;
			}

			&.qodef-btn-light-style {
				color: #585858;
				background-color: #fff;
				border-color: #fff;

				&:hover {
					color: #fff !important;
					background-color: transparent !important;
					border-color: #fff !important;
				}
			}
		}
	}

	@include ipad-landscape {
		.qodef-content {
			height: 100%;
			min-height: 100vh;
		}

		&.admin-bar {
			.qodef-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
	position: fixed;
	bottom: 0;
	right: 0;
	margin: 0;
	z-index: 10000;
	opacity: 0;
	visibility: hidden;
	font-family: $default-heading-font;
	font-size: 12px;
	letter-spacing: 0.25em;
	color: #fff;
	mix-blend-mode: difference;
	height: 40px;
	line-height: 40px;
	padding: 0 20px;
	transform-origin: 100% 0;
	transform: rotate(-90.2deg) translate(164px, -57px);

	@media screen and (max-width: 680px) {
		transform: rotate(-90.2deg) translate(164px, -40px);
	}

	&.off {
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.15s ease, visibility .0s .15s;
	}

	&.on {
		opacity: 1;
		visibility: visible;
		transition: opacity 0.3s ease;
	}

	.qodef-back-to-top-text {
		position: relative;
		display: block;
		text-align: center;
		text-transform: uppercase;
		transition: all .25s ease-in;

		i {
			transition: .15s ease .05s;
		}

		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: -24px;
			width: 15px;
			height: 1px;
			background-color: currentColor;
			opacity: 1;
			filter: blur(); //blur fix
			transform: scaleX(1);
			transform-origin: 100% 50%;
			transition: .3s;
		}

		&:hover {

			&:after {
				animation: qodef-btt-animation 1s forwards;
				
			}
		}

		@keyframes qodef-btt-animation {
			0% {
				transform: scaleX(1);
				transform-origin: 100% 50%;
			}
			39% {
				transform: scaleX(0);
				transform-origin: 100% 50%;
			}
			40% {
				transform: scaleX(0);
				transform-origin: 0 50%;
			}
			100% {
				transform: scaleX(1);
				transform-origin: 0 50%;
			}
		}

	}
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.qodef-self-hosted-video-holder {
	.qodef-video-wrap {
		.mejs-poster,
		.mejs-container,
		.mejs-overlay-play {
			height: 100% !important; /*!important because of the inline style*/
			width: 100% !important; /*!important because of the inline style*/
		}
	}
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
#Row Background styles - begin
========================================================================== */
.qodef-row-has-background-text {
	position: relative;

	> .wpb_column {
		z-index: 10;
	}
}

.qodef-row-background-corner-holder {
	opacity: 0;
	position: absolute;
	display: block;
	z-index: 4;
	width: 50%;
	height: 100%;
	background-color: $first-main-color;
	overflow: hidden;
	transition: opacity .3s ease-in-out;

	.qodef-fully-loaded &{
		opacity: 1;
	}

	&.top-left {
		left: 0;
		top: 0;
		background: linear-gradient(to top left, transparent 50%, $first-main-color 50%);
	}

	&.top-right {
		right: 0;
		top: 0;
		background: linear-gradient(to top right, transparent 50%, $first-main-color 50%);
	}

	&.bottom-left {
		left: 0;
		bottom: 0;
		background: linear-gradient(to bottom left, transparent 50%, $first-main-color 50%);
	}

	&.bottom-right {
		right: 0;
		bottom: 0;
		background: linear-gradient(to bottom right, transparent 50%, $first-main-color 50%);
	}
}

.qodef-row-background-custom-holder {
	opacity: 0;
	@include qodefAbsoluteHolderLayout();
	z-index: 5;
	overflow: hidden;
	transition: opacity .3s ease-in-out;

	.qodef-fully-loaded &{
		opacity: 1;
	}

	.qodef-row-background-text-wrapper {
		@include qodefTableLayout();

		.qodef-row-background-text-wrapper-inner {
			display: table-cell;
			vertical-align: top;
			font-family: $default-text-font;
			color: $first-main-color;
			font-size: 240px;
			font-weight: 700;
			letter-spacing: 0;

			.qodef-row-background-text-1 {
				line-height: 1.2;
				transform: translateY(-17px);
			}
		}

		@include ipad-landscape {
			display: none;
		}

		&.qodef-row-background-text-animation {
			visibility: hidden;

			.no-touch & {
				.qodef-char-mask {
					display: inline-block;
					vertical-align: top;
					overflow: hidden;

					> span {
						display: inline-block;
						transform: translate3d(-100%, 0, 0);
						transition: all .5s cubic-bezier(0.05, 0.43, 0.04, 0.87);
					}
				}

				&.qodef-row-background-text-align-right {
					.qodef-char-mask > span {
						@include qodefTransform(translate3d(100%, 0, 0));
					}
				}

				.qodef-char-mask > span.qodef-show {
					@include qodefTransform(translate3d(0, 0, 0));
				}
			}
		}
	}
}

/* ==========================================================================
   #Row Background styles - end
   ========================================================================== */

.qodef-content-inner {
	transition: background-color .3s ease-out;
}

.qodef-search-opened {

	.qodef-content {
		overflow: hidden;

		.qodef-content-inner {
			filter: blur(3px);
			background-color: rgba(0, 0, 0, .4);
		}
	}
}

