   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 0;
  z-index: 10000;
  opacity: 0;
  visibility: hidden;
  font-family: "Oswald", sans-serif;
  font-size: 12px;
  letter-spacing: 0.25em;
  color: #fff;
  mix-blend-mode: difference;
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  transform-origin: 100% 0;
  transform: rotate(-90.2deg) translate(164px, -57px);
}

@media screen and (max-width: 680px) {
  #qodef-back-to-top {
    transform: rotate(-90.2deg) translate(164px, -40px);
  }
}

#qodef-back-to-top.off {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.15s ease, visibility .0s .15s;
}

#qodef-back-to-top.on {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease;
}

#qodef-back-to-top .qodef-back-to-top-text {
  position: relative;
  display: block;
  text-align: center;
  text-transform: uppercase;
  transition: all .25s ease-in;
}

#qodef-back-to-top .qodef-back-to-top-text i {
  transition: .15s ease .05s;
}

#qodef-back-to-top .qodef-back-to-top-text:after {
  content: '';
  position: absolute;
  top: 50%;
  left: -24px;
  width: 15px;
  height: 1px;
  background-color: currentColor;
  opacity: 1;
  filter: blur();
  transform: scaleX(1);
  transform-origin: 100% 50%;
  transition: .3s;
}

#qodef-back-to-top .qodef-back-to-top-text:hover:after {
  animation: qodef-btt-animation 1s forwards;
}

@keyframes qodef-btt-animation {
  0% {
    transform: scaleX(1);
    transform-origin: 100% 50%;
  }
  39% {
    transform: scaleX(0);
    transform-origin: 100% 50%;
  }
  40% {
    transform: scaleX(0);
    transform-origin: 0 50%;
  }
  100% {
    transform: scaleX(1);
    transform-origin: 0 50%;
  }
}

